.h1 {
    font-size: 30px;
    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
}

.search {
    width: 40%;
    min-width: 200px;
    max-width: 600px;
    padding: 10px;
    margin: 10px 0 40px;
    border-radius: 2px;
    color: #ffffff;
    font-size: 16px;
    background: rgba(255,255,255, 0.3);
    border: 1px solid white;
}
.gif-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 0 10px;
    grid-auto-rows: 10px;
}

.gif-list img {
    width: 310px;
}
body {
    font-family: Arial;
    text-align: center;
    padding: 0 20px;
    min-width: 370px;
    background: rgb(255,95,109);
    background: linear-gradient(90deg, 
        rgba(255,95,109,1) 0%, 
        rgba(255,195,113,1) 100%
    );
}

